// CardSection.js
import React from 'react';
import {Link} from 'react-router';
import {CardElement, CardNumberElement, CardExpiryElement, CardCVCElement, PostalCodeElement} from 'react-stripe-elements';
import visaImg from '../assets/visa.png';

class CardSection extends React.Component {

  constructor(props){
    super(props);
  }

  render() {
    const btnClass = this.props.isUser ? 'btn btn-green btn-save' : 'btn btn-green btn-checkout';
    const btnText = this.props.isUser ? 'Update and Save' : 'CHECKOUT NOW ➡';
    const btnLink = this.props.isUser ? '' : '/buy-droplet';
    return (
      <div className="stripe-ui">
        <div className="detail-section">
        <img src={visaImg} className="visa-img"/>
      <div className="form-group">
          <label htmlFor="creditNumber">Credit Card Number</label>
          <CardNumberElement/>
      </div>
      <div className="form-group">
          <label htmlFor="creditName">Name on Credit Card</label><br/>
          <input type="text" className="form-control has-icon" id="creditName"/>
      </div>
      <div className="row">
          <div className="col-3">
              <div className="form-group">
                  <label htmlFor="selectMonth">Expiry Month</label>
                  <CardExpiryElement/>
              </div>
          </div>
          <div className="col-3">
              <div className="form-group">
                  <label htmlFor="creditNumber">Security Code</label>
                  <CardCVCElement/>
              </div>
          </div>
          </div>
      </div>
      <div className="form-check checkbox">
      <label className="form-check-label green-checkbox">
          <input type="checkbox" className="form-check-input"/>
          <i className={this.props.isUser ? "" : "helper"}></i>
          <i><span>{this.props.isUser ? '' : 'Yes, save my card details for future purchases.'}</span></i>
      </label>
      </div>

      <div className="row col-12">
      <button className={btnClass}><Link to={btnLink}>{btnText}</Link></button>
      </div>
      </div>



    );
  }
}

export default CardSection;



