import React from 'react';
import './PaymentDetail.scss';

import StripeUITest from '../../Widgets/StripeUITest/components/StripeUITest.js';
import Header from '../../Header/components/Header.js';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class PaymentDetailComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    onChange = () => {
    }

    goToBuyDroplet = () => {
        this.props.router.push('/buy-droplet');
    }

    goToIndex = () => {
        this.props.router.push('/');
    }

    render() {
        return (
            <div className="payment-detail">
                <Header goHome={this.goToIndex} isLoggedIn={true} />
                <div className="progress">
                    <div className="finish-progress"></div>
                </div>

                <div className="container history-container">

                    <div className="row">
                        <div className="offset-1 col-10">
                            <div className="step-section">
                                <h4 className="step-text">Step 4 : Payment Details</h4>
                            </div>

                            <StripeUITest isUser={false}/>


                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default PaymentDetailComponent;



